@import url(header/header.css);
@import url(burger/burger.css);
@import url(hero-form/hero-form.css);
@import url(hero/hero.css);
@import url(statistics/statistics.css);
@import url(gallery/gallery.css);
@import url(download-now/download-now.css);
@import url(faq/faq.css);
@import url(footer/contact-us/contact-us.css);
@import url(footer/about/about.css);
@import url(footer/footer-last/footer-last.css);
@import url(calendar/calendars.css);
@import url(autocomplete/autocomplete.css);
:root {
  --linear-gradient: linear-gradient(#f5f5f5, #ddecff, #f5f5f5);
  --primary-blue-train: #0578ff;
  --primary-blue: #0578FF;
  --primary-black-train: #202020;
}

* {
  box-sizing: border-box;
  font-family: "Poppins";
  scroll-behavior: smooth;
}

#contact {
  scroll-margin-top: 100px;
}

body {
  margin: 0;
  display: block;
}

h2 {
  color: var(--primary-black-train);
  font-weight: bold;
  font-size: 32px;
}

.container {
  width: 100%;
  padding: 0 20px;
}
@media screen and (min-width: 576px) {
  .container {
    padding: 0 32px;
  }
}
@media screen and (min-width: 992px) {
  .container {
    padding: 64px;
  }
}

.three-first-sections {
  background: var(--linear-gradient);
}

@media screen and (min-width: 576px) {
  h2 {
    color: var(--primary-black-train);
    font-weight: bold;
    font-size: 64px;
  }
}
a {
  color: inherit;
  text-decoration: none;
  transition: all 0.3 ease;
}
a:hover {
  color: white;
  text-decoration: underline;
  opacity: 0.85;
}

button, .submit {
  transition: all 0.3 ease;
  cursor: pointer;
}
button:hover, .submit:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 16px rgba(0, 0, 0, 0.2);
  opacity: 0.95;
}